import React, {Component} from 'react';
import "./ContactUs.scss";
import {validateEmail} from "../../Utils";

class ContactUs extends Component {

    state = {
        form: {
            firstName: "",
            lastName: "",
            email: "",
            phoneNumber: ""
        },
        formErrors: {email: ""},
        formValidated: false
    };

    componentDidMount() {
    }

    onFormChange = (event) => {
        if (!event.target || !event.target.name) {
            return;
        }
        let form = {...this.state.form, [event.target.name]: event.target.value};

        this.setState(state => {
            return {...state, form: form}
        });

        this.validateForm();
        console.log("Email", validateEmail(this.state.form.email));


    };

    validateEmail = () => {
        let error = "";
        if (!validateEmail(this.state.form.email)) {
            error = "This is not a correctly formatted Emil.";
        }
        let formErrors = {...this.state.formErrors, email: error};
        this.setState({formErrors: formErrors})


    };

    submitForm = (event) => {
        event.preventDefault();
        console.log("Submit", this.state.form);
    };

    validateForm = () => {
        let form = this.state.form;
        let success = true;
        for (let key in form) {
            if (form.hasOwnProperty(key) && !form[key]) {
                success = false;
                if (key === 'email') {
                    this.validateEmail();
                }
            }
        }

        this.setState({formValidated: success});
    };


    render() {

        const {form, formValidated, formErrors} = this.state;

        return (
            <div id="ContactUs">

                <h1 className="text-align-center font-weight-light color-primary">Start Here!</h1>

                <form onSubmit={this.submitForm} className="form">

                    <div className="inputLabel">
                        <label required={true}>First Name</label>
                        <input value={form.firstName} onChange={this.onFormChange} name={"firstName"}
                               placeholder="First Name"
                               className="input" type="text"/>
                    </div>

                    <div className="inputLabel">
                        <label required={true}>Last Name</label>
                        <input value={form.lastName} onChange={this.onFormChange} name={"lastName"}
                               placeholder="Last Name"
                               className="input" type="text"/>
                    </div>

                    <div className="inputLabel">
                        <label required={true}>Email</label>
                        <div>
                            <input value={form.email} onChange={this.onFormChange} name={"email"}
                                   placeholder="Email"
                                   className="input" type="email"/>
                            <span className="formError">{formErrors.email}</span>
                        </div>
                    </div>

                    <div className="inputLabel">
                        <label required={true}>Phone Number</label>
                        <input value={form.phoneNumber} onChange={this.onFormChange} name={"phoneNumber"}
                               placeholder="+1 555 5555"
                               className="input" type="tel"/>
                    </div>


                    <button disabled={!formValidated} id="submitButton" type="submit"
                            className="standard-button">Submit
                    </button>

                </form>

            </div>
        );
    }
}

export default ContactUs;
